import React from "react";


function Title(props){
    return <div style={{
        width: '177px',
        padding: 3,
        border: '3px solid lightgrey',
        display: 'table-cell',
        fontSize: '2em',
        boxShadow: '1px 1px 0px 1px black'
    }}>
        <div style={{
            
            borderTop: '2px solid black',
            borderLeft: '2px solid black',
        }}><b>{props.name}</b>
        </div>
        

    </div>

}

export default Title;