import React from "react";

function Inblock(props){
    return <div style= {{
        width: '177px',
        height: '53px',
        padding: 3,
        border: '3px solid lightgrey',
        display: 'table-cell',
        fontSize: '2em',
        boxShadow: '1px 1px 0px 1px black'
    }}>
        <div style={{
            borderTop: '2px solid black',
            borderLeft: '2px solid black',
        }}>{props.inhtml}
        </div>

        <div style={{
            borderLeft: '2px solid black',
        }}>{props.incss}
        </div>

        <div style={{
            borderLeft: '2px solid black',
        }}>{props.injscript}
        </div>

        <div style={{
            borderLeft: '2px solid black',
        }}>{props.inreact}
        </div>

    </div>
}
export default Inblock;